*{
    margin: 0;
    box-sizing: border-box;
}

body{
    background-color: #000000;
    height: 100vh;
    width: 1440px;
}

nav{
    color: white;
    font-family: "Inter",sans-serif;
    display: flex;
    align-items: center;
    margin: 40px 240px;
}

.icons{
    width: 190px;
    display: flex;
    justify-content: space-between;
}

nav > h2 {
    margin-right: auto;
    font-weight: 400;
    font-size: 72px;
}

.main-info{
    display: grid;
    grid-template-columns: 459px 484px;
    max-width:999px;
    align-items: center;
    justify-content: center;
    margin: 100px 210px;
}

.name-info{
    font-family: 'Outfit';
    font-style: normal;
    font-weight: 200;
    font-size: 72px;
    line-height: 91px;
    color: #C3FCF1;
    grid-row: 1;
}

.role-info{
    font-family: 'Outfit';
    font-style: normal;
    font-weight: 700;
    font-size: 72px;
    line-height: 91px;
    color: #FFF300;
}
.img-info{
    grid-row: 1 / span 3;
    margin-left: 100px;
    width: 400px; 
    margin-top: -115px;
}

.about-info{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    color: #FFFFFF;
    width: 384px;
    margin-top: 25px;
}

.cta-btn{
    background: #C3FCF1;
    width:264px;
    height: 86px;
    border-radius: 30px;  
    font-family: 'Monda';
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 46px;
    letter-spacing: 0.03em;
    color: #000000;
    margin: -33px 328px ;
}

.scroll,.scroll-arrow{
    color: #ffffff;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
}

.scroll{
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.5em;
    text-align: center;
    margin-top: 140px;
}

.scroll-arrow{
    font-size: 32px;
    line-height: 39px;
    letter-spacing: 0.1em;
    transform: rotate(180deg);
    text-align: center;
}

